@import '../../style/config';

.header-box {
    width: @section-width;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    color: #000;

    >div {
        flex-shrink: 0;
    }

    .left {
        display: flex;
        align-items: center;

        .logo {
            width: 180px;
            margin-right: 155px;
        }
        .nav-item {
            display: flex;
            align-items: center;
            cursor: pointer;
            color:#000;
        }

        .current {
            color: @primary-color;
        }

        .nav-item:hover {
            opacity: .6;
        }

        .nav-item+.nav-item {
            margin-left: 60px;
        }

        .arrow-right::after {
            content: '<';
            display: inline-block;
            margin-left: 10px;
            transform: rotate(-90deg)scaleY(2);
        }
    }

    .right {
        display: flex;
        align-items: center;
        @radius: 8px;
        @size: 36px;

        >div {
            cursor: pointer;
        }

        >div:hover {
            opacity: .6;
        }

        .cart-box {
            width: @size;
            height: @size;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: @primary-color;
            border-radius: @radius;
        }

        .login {
            margin-left: 20px;
            height: @size;
            color: #fff;
            width: 120px;
            background-color: @primary-color;
            border-radius: @radius;
            display: flex;
            justify-content: center;
            align-items: center;
        }

    }


}