@import '../../style/config';

.box {
    background-color: #222;
    color: #fff;

    .main {
        width: @section-width;
        margin: 0 auto;
        padding: 44px 0px 100px;

        >div {
            display: flex;
            align-items: center;
        }

        >div.line1 {
            img:first-child {
                margin-left: 17px;
            }

            img+img {
                margin-left: 10px;
            }
        }

        .subsection {
            margin-top: 50px;

            span {
                margin-left: 30px;
            }
        }

        .links {
            margin-top: 46px;

            a {
                color: #fff;
            }

            a:first-child {
                margin-left: 30px;
            }

            a+a {
                margin-left: 20px;
            }
        }

        .hr {
            height: 1px;
            background-color: #fff;
            margin: 40px 0 65px;
        }

        .contact {
            position: relative;
            display: flex;
            justify-content: space-between;
            font-weight: bold;
            overflow: hidden;

            .logo {
                position: absolute;
            }

            .mask-contact:after {
                position: relative;
                bottom: -40px;
                font-family: Arial;
                content: 'CONTACT US';
                font-size: 72px;
                color: rgba(255, 255, 255, 0.2);
            }

            .left {
                .title {
                    font-size: 48px;
                }

                img {
                    margin-top: 60px;
                }
            }

            .right {
                font-size: 24px;

                .line1 {
                    margin-bottom: 67px;
                }

                .line2 {
                    margin-bottom: 55px;
                }

                >div {
                    >div:first-child {
                        display: flex;
                        align-items: center;
                    }

                    img {
                        margin-right: 20px;
                        width: 25px;
                    }

                    >div:last-child {
                        margin-left: 45px;
                        margin-top: 38px;

                        .tel {
                            margin-left: 73px;
                        }
                    }

                }

            }

        }
    }

    .btm {
        text-align: center;
        padding: 15px;

        span {
            margin-left: 80px;
        }
    }
}